import React, { useEffect, useCallback } from 'react'
import {
    View,
    Text,
    StyleSheet,
    Image,
    Button,
    ScrollView,
    SafeAreaView,
    Alert, FlatList, ActivityIndicator, TouchableOpacity
} from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import { THEME } from '../../theme'
import {
    toogleBooked,
    removePost,
    setSelectAdres,
    setSelectAdresAffiliate,
    addProductBasket, addProductBasketOff, setTypeDeliverySystem
} from '../../store/actions/post';

import { Feather } from '@expo/vector-icons';
import {OneAdressAffiliate} from "./OneAdresAffiliate";
import CheckBasketSelectAdresDelivered from "../../components/SystemFunction/CheckBasketSelectAdresDelivered";
import {ButtonEbee} from "../../templateComponents/Button/ButtonEbee";


export const AffiliateAdresListScreen = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const postId = route.params.postId;

    const basket = useSelector(state => state.post.basket);
    const basketOff = useSelector(state => state.post.basketOff);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);

    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);
    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const ActiveDelivered = useSelector(state => state.post.activeDelivered);

    // const product = useSelector(state =>
    //     state.post.allPosts.find(p => p.id === postId)
    // )

    const product = route.params.productData;
    const AdresList = useSelector(state => state.post.adressAffiliate.list);


    const ListAdresForRender = Object.keys(AdresList).map(
        (id,n)=>{
            return AdresList[id]
        }
    );


    const handleChekerDeliveryType = async ()=>{
        await dispatch(setTypeDeliverySystem(true));
        navigation.navigate('AdressList', {
            productId: 1,
            title: "Доставка",
            booked: false,
            returnBasket: true,
            productData: [{id: 1, title: "Доставка", booked: false}]
        });
    }



    const toggleHandler = useCallback(() => {
        dispatch(toogleBooked(product))
    }, [dispatch, product])

    useEffect(() => {
        navigation.setParams({ toggleHandler })
    }, [toggleHandler]);

    const RenderAdresList = ({dataAdres,onPress,statusDelivery = false})=>{
        // console.log(dataAdres);
        return (
            <OneAdressAffiliate dataAdress={dataAdres} onPress={onPress} statusDelivery={statusDelivery}/>
        )
    };

    if (!product) {
        return null
    }


    const loadAdressAffiliate = useSelector(state => state.post.loadAdressAffiliate);

    if(!loadAdressAffiliate){
        return(
            <SafeAreaView style={styles.center}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
            </SafeAreaView>
        );
    }


    const handlPressOneAdresAffiliate = async (idAdres)=>{
        //console.log('Нажали на адрес самовывоза - ',idAdres);
        await dispatch(setSelectAdresAffiliate(idAdres));
        let NewBasketData = await CheckBasketSelectAdresDelivered(basket,DeliveryAdresSystem,idAdres,TypeDeliverySystem,basketOff);
        await dispatch(addProductBasket(NewBasketData.OkBasket));
        await dispatch(addProductBasketOff(NewBasketData.OffBasket));
        window.location.hash = 'Main';
        navigation.navigate('Main');
    };


    return (
        <SafeAreaView style={styles.BodyView}>
            {ActiveDelivered ?
                <View style={{paddingHorizontal: "1%", display: "flex", justifyContent: "cener"}}>
                    <ButtonEbee size={'l'}
                                borderRadius={25}
                                style={{
                                    backgroundColor: "#ffffff",
                                    color: THEME.MAIN_COLOR,
                                    borderColor: THEME.MAIN_COLOR,
                                    borderWidth: 1
                                }}
                                onPress={handleChekerDeliveryType}>Хочу доставку!</ButtonEbee>
                </View>
                :
                false
            }
            <FlatList
                data={ListAdresForRender}
                keyExtractor={item => "Blocks_Adress_"+item.id}
                renderItem={
                    ({item})=>(
                        <RenderAdresList dataAdres={item} onPress={handlPressOneAdresAffiliate} statusDelivery={true}/>
                    )
                }>
            </FlatList>
        </SafeAreaView>
    )
}

AffiliateAdresListScreen.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title
    const booked = route.params.booked;
    const toggleHandler = route.params.toggleHandler;
    const iconName = booked ? 'ios-star' : 'ios-star-outline'
    return {
        headerTitle: 'Выбрать точку самовывоза',
    }
};

const styles = StyleSheet.create({
    center:{
        flex:1,
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center'
    },
    newAdresButton:{
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center',
        width:"100%",
        // backgroundColor:"#333333",
        paddingHorizontal:"3%",
        paddingVertical:"4%"
    },
    newAdresText:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:18,
        color:THEME.MAIN_COLOR,
    }
});
