import React from 'react';
import {Text} from "react-native";
import { Feather } from '@expo/vector-icons';
import {THEME} from "../../../theme";
import {useSelector} from "react-redux";


export const ErrorPromokodeIconInfo = ()=>{

    const Shows = useSelector(state => state.post.promokodeErrorInfo);

  //  console.log(Shows);

    if(Shows.showIconError){
        return(
            <Text style={{textAlign:"left",position:"absolute",left:"3%"}}>
                <Feather name="alert-circle" size={32} color={"#ffffff"} />
            </Text>
        )
    }else{
        return(
            <Text style={{textAlign:"left",position:"absolute",left:"3%"}}>
                <Feather name="check-circle" size={32} color={"#ffffff"} />
            </Text>
        )
    }
}